@mixin borderRadiusComponent {

	.square,
	.radius-none {
		border-radius: 0; }

	@for $i from 1 through $borderRadiusMax {
		.radius-#{$i} {
			$radiusValue: $i * $borderRadiusIncreasedBy;
			border-radius: $radiusValue + px; } }

	.circle {
		border-radius: 100%; } }
