@mixin paddingComponent {

	.#{$breakpointPrefix}padding {

		@each $side in $frameSides {
			&-#{$side} {
				&-none {
					@if $isFirstBreakpointPrefix {
						@extend .padding-#{$side}-none; }

					@else {
						padding-#{$side}: 0; } }

				$sizeCounter: $frameSizeIncreasedBy;
				@while $sizeCounter <= $frameMaxSize {
					&-#{$sizeCounter} {
						@if $isFirstBreakpointPrefix {
							@extend .padding-#{$side}-#{$sizeCounter} !optional; }

						@else {
							padding-#{$side}: $sizeCounter * 1rem; } }
					$sizeCounter: $sizeCounter + $frameSizeIncreasedBy; } } }

		&-x {
			&-none {
				@if $isFirstBreakpointPrefix {
					@extend .padding-left-none;
					@extend .padding-right-none; }

				@else {
					@extend .#{$breakpointPrefix}padding-left-none;
					@extend .#{$breakpointPrefix}padding-right-none; } }

			$sizeCounter: $frameSizeIncreasedBy;
			@while $sizeCounter <= $frameMaxSize {
				&-#{$sizeCounter} {
					@extend .#{$breakpointPrefix}padding-left-#{$sizeCounter};
					@extend .#{$breakpointPrefix}padding-right-#{$sizeCounter}; }
				$sizeCounter: $sizeCounter + $frameSizeIncreasedBy; } }

		&-y {
			&-none {
				@if $isFirstBreakpointPrefix {
					@extend .padding-top-none;
					@extend .padding-bottom-none; }

				@else {
					@extend .#{$breakpointPrefix}padding-top-none;
					@extend .#{$breakpointPrefix}padding-bottom-none; } }

			$sizeCounter: $frameSizeIncreasedBy;
			@while $sizeCounter <= $frameMaxSize {
				&-#{$sizeCounter} {
					@extend .#{$breakpointPrefix}padding-top-#{$sizeCounter};
					@extend .#{$breakpointPrefix}padding-bottom-#{$sizeCounter}; }
				$sizeCounter: $sizeCounter + $frameSizeIncreasedBy; } }

		&-none {
			@if $isFirstBreakpointPrefix {
				@extend .padding-top-none;
				@extend .padding-bottom-none;
				@extend .padding-left-none;
				@extend .padding-right-none; }

			@else {
				@extend .#{$breakpointPrefix}padding-top-none;
				@extend .#{$breakpointPrefix}padding-bottom-none;
				@extend .#{$breakpointPrefix}padding-left-none;
				@extend .#{$breakpointPrefix}padding-right-none; } }

		$sizeCounter: $frameSizeIncreasedBy;
		@while $sizeCounter <= $frameMaxSize {
			&-#{$sizeCounter} {
				@if $isFirstBreakpointPrefix {
					@extend .padding-x-#{$sizeCounter};
					@extend .padding-y-#{$sizeCounter}; }

				@else {
					@extend .#{$breakpointPrefix}padding-x-#{$sizeCounter};
					@extend .#{$breakpointPrefix}padding-y-#{$sizeCounter}; } }

			$sizeCounter: $sizeCounter + $frameSizeIncreasedBy; } } }
