@mixin globalSetupComponent {

	*,
	*::before,
	*::after {
		box-sizing: border-box; }

	html,
	body {
		min-width: 100%;
		min-height: 100vh; }

	body {
		line-height: $defaultLineHeight;
		font-size: $defaultFontSize; }

	p, ul, ol, dl, table,
	blockquote, pre, figure,
	hr {
		padding-top: 0;
		padding-bottom: 0;
		margin-top: 0;
		margin-bottom: 1rem; }

	h1, h2, h3, h4, h5, h6 {
		margin-top: 0;
		margin-bottom: .5rem; } }
